import {createRouter, createWebHistory} from 'vue-router'


import Home from '@/pages/Home.vue'



const routers = createRouter({
    history: createWebHistory(),
    routes: [
        { path: '/', name: 'HomePage', component: Home},
        { path: '/home', name: 'Home', component: () => import('@/pages/Home.vue')},
        { path: '/History', name: 'History', component: () => import('@/pages/History.vue')},
        { path: '/extra', name: 'extraPage', component: () => import('@/pages/Extra.vue')},
        { path: '/law', name: 'lawPage', component: () => import('@/pages/Law.vue')},
        { path: '/Add',name: 'Add',component: () => import('../pages/Add.vue')},
        { path: '/Add2',name: 'Add2',component: () => import('../pages/Add2.vue')},
        { path: '/Add3',name: 'Add3',component: () => import('../pages/Add3.vue')},
        { path: '/Add4',name: 'Add4',component: () => import('../pages/Add4.vue')},
        { path: '/Add5',name: 'Add5',component: () => import('../pages/Add5.vue')},
        { path: '/Add6',name: 'Add6',component: () => import('../pages/Add6.vue')},
        { path: '/Biography',name: 'Biography',component: () => import('../pages/Biography.vue')},
        { path: '/:pathMath(.*)', name: 'not-found', component: () => import('@/pages/404.vue')},


    ]
})  


export default routers