                <h2 class="intro__box-title">Профессия - учитель:</h2>
                   <p class="intro__box-text">Интерес к истории пробуждался постепенно, как аппетит у человека (который, как известно, «приходит во время еды»). Началось всё со школьных уроков, с рассказов учителей о делах, давно минувших: о преданности и предательствах, о подвигах и трусости. Как черное и белое, человеческие качества сходились на полотне истории и образовывали там небывалое переплетение красок. По-настоящему увлек меня в мир истории мой отец, кандидат исторических наук, доцент В.Н.Трухачев. Итак, историк – это исследователь, который должен отделить пшеницу от плевел, а если это невозможно, то хотя бы попытаться. Учась на историческом факультете ТГПУ, я вернулся в свою школу. Об этом я мечтал с последнего звонка. Но в 2001 году я стал преподавателем в Ташкентском Политехническом профессиональном колледже. Работа преподавателя оказалась не такой простой, как мне представлялось в школьные годы. Слово работа здесь не совсем подходит. Служение… Преподаватель – в некотором роде священнослужитель, он поставлен обществом выполнять важнейшую функцию – передавать подрастающему поколению накопленный человечеством опыт. От того, как преподаватель относится к своему занятию, зависит очень многое. Сердце каждого ученика открывается по-разному. Надо только найти ключик. С группой надо подружиться, видеть в учащихся не объект воспитательной деятельности, а товарищей. Преподаватель, по моему твердому убеждению, – это тот человек, в котором учащийся может обрести друга и помощника, советника. При этом, безусловно, необходимо держать определенную дистанцию, иначе учебный процесс остановится вовсе. Задача педагога – дать путь к познанию для всех и каждого. Учащимся должно быть интересно учиться. Поэтому хорошо, когда преподаватель включает в учебный процесс элементы игры и состязательности. Также росту интереса к истории способствуют различные мероприятия во внеурочное время: викторины, конкурсы, посещение музеев, представления, просмотры исторических фильмов. Мне очень близка мысль, сформулированная давно, но от этого не ставшая менее актуальной: основная цель деятельности преподавателя – не «научить», а способствовать формированию у детей навыков самостоятельного мышления и поиска. Именно такие граждане нужны нашей стране: творческие, активные, смелые. И если в этом будущем будет частичка моих творческих усилий, я буду считать свою деятельность не напрасной.</p> 
